import React from 'react'
import {
  Navbar,
  NavbarGroup,
  NavbarDivider,
  Alignment,
  Colors,
  Icon
} from '@blueprintjs/core'
import styled from 'styled-components'
import { StaticImage } from 'gatsby-plugin-image'
import { getSpacing } from '../stylesheet'

const ContentContainer = styled.div`
  padding: 2% 20%;
  @media only screen and (max-width: 420px) {
    padding: 2% 2%;
  }
`

const StyledNavbar = styled(Navbar)`
  padding-top: ${getSpacing(1)};
  height: 60px;
`

const StyledRightNavbarGroup = styled(NavbarGroup)`
  @media only screen and (max-width: 420px) {
    display: none;
  }
`

const Link = styled.a`
  padding: ${getSpacing(2)};
  :hover {
    background-color: ${Colors.DARK_GRAY4};
  }
`

const ICON_HEIGHT = 24

export const SiteLayout = ({ children }) => {
  return (
    <div className="bp3-dark">
      <StyledNavbar>
        <NavbarGroup align={Alignment.LEFT}>
          <Link href="https://jamesdhw.github.io">
            <Icon size={ICON_HEIGHT} color="white" icon="home" />
          </Link>
          <NavbarDivider />
          <Link href="/">
            <Icon
              size={ICON_HEIGHT}
              color="white"
              icon="book"
              intent="success"
            />
          </Link>
        </NavbarGroup>

        <StyledRightNavbarGroup align={Alignment.RIGHT}>
          <NavbarDivider />
          <Link target="_blank" href="https://github.com/JamesDHW">
            <StaticImage
              height={ICON_HEIGHT}
              src="../images/icons/GitHub.png"
            />
          </Link>
          <Link target="_blank" href="https://www.linkedin.com/in/jamesdhw/">
            <StaticImage
              height={ICON_HEIGHT}
              src="../images/icons/LinkedIn.png"
            />
          </Link>
        </StyledRightNavbarGroup>
      </StyledNavbar>

      <ContentContainer>{children}</ContentContainer>
    </div>
  )
}
